@import url(https://fonts.googleapis.com/css?family=Product+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Product Sans', cursive;
}

h2 {
  text-align: center;
}

#mapContainer {
  text-align: center;
}

#map {
  height: 80vh;
  width: 80vw;
  display: inline-block;
  border-radius: 5px;
}

.leaflet-popup-content-wrapper {
  max-height: 200px;
  overflow-y: auto;
}

#radioButtonGroup {
  padding-bottom: 15px;
  text-align: center;
}

input {
  cursor: pointer;
}

p {
  text-align: center;
}

.credits {
  margin-top: 2vh;
  text-align: center;
}

.credits p {
  width: 80vw;
  display: inline-block;
  text-align: center;
  font-size: small;
}
